import React from "react"

// import Layout from "../components/layout"
import SEO from "../components/seo"
import { globalHistory } from "@reach/router";
import SpinningImage from "../components/spinning-image";
import error from "../images/svg/error.svg"

function parseUri() {
  let path = globalHistory.location.pathname.substring(1);
  let search = globalHistory.location.search;
  return decodeURI(path + search);
}

const NotFoundPage = () => (
  // <Layout>
  //   <SEO title="404: Not found" />
  //   <h1>NOT FOUND</h1>
  //   <p>You just hit a route that doesn&#39;t exist... the sadness.</p>
  // </Layout>
  <>
    <SEO title={globalHistory.location.pathname.substring(1, 10) + "..."} />
    <SpinningImage image={error} text={parseUri()}/>
  </>
)

export default NotFoundPage
